import { Component, OnInit } from '@angular/core';
import { StudentSurvey } from '../student-survey';
import { ApiService } from '../api.service';

/*
Author : Devika Walavalkar
This file is the business logic for updating/deleting survey data for any record.
It calls the get api to search any record, update and delete to perform respective operations
*/

@Component({
  selector: 'app-update-delete',
  templateUrl: './update-delete.component.html',
  styleUrls: ['./update-delete.component.css']
})
export class UpdateDeleteComponent implements OnInit {

  constructor(private apiService: ApiService ) { }
  survey = new StudentSurvey;
  submitted = false;
  surveyList : any;
  fname_curr : any;
  lname_curr : any;
  sample : any;

  ngOnInit() {
  }

  onSubmit(){
    this.sample = JSON.stringify(this.survey);
    this.apiService.updateStudentData(this.sample).subscribe((res)=>{
      console.log("Student Survey Saved!");
    });
    this.submitted = true;
  }

  searchItem(sid:string) {
    this.apiService.getItem(sid).subscribe((res)=>{
      console.log(res);
      this.surveyList = res;
      this.fname_curr = this.surveyList.student_fname;
      this.lname_curr = this.surveyList.student_lname;
    });
  }

  deleteItem(sid:string) {
    this.apiService.deleteStudentData(sid).subscribe((res)=>{
      console.log("Deleted");
      this.submitted = true;
    });
  }

}
